<template>
		<div class="set" ref="set">
			<loading v-if="!loaded"></loading>
			<div class="wrap" v-else>
				<div class="set-title" ref="setTitle">
					<div class="set-title-wrap">
						<img :src="imgSrc" class="set-title-bg">
						<div class="set-intro">
							<div class="set-intro-img">
								<img :src="imgSrc" >
							</div>
							<slot name="title"></slot>
							
						</div>
						<div class="set-play-all">
							<div class="ran-play-all-btn"><a href="">播放全部</a></div>
						</div>
					</div>
				</div>
				<div class="panel" ref="panel" @scroll="scrollEvent">
					<slot class="set-music-list" name="list">
						
					</slot>
				</div>
			</div>
				
		</div>

</template>

<script>
import Loading from "components/base/Loading"
import {getMusicList} from "common/js/music"
import {getSet} from "api/song"
import {ERR_OK} from "api/config"
import {mapGetters} from "vuex"
export default{ 
	name:"set",
	components:{
		Loading
	},
	props:{
		imgSrc:{
			type:String,
			default:""
		},
		loaded:{
			type:Boolean,
			default:false
		}
	},
	methods:{
		scrollEvent(){
			let setTile=this.$refs.setTitle,
			panel=this.$refs.panel;
			let change=550-panel.scrollTop;
			if(change>=150){
				setTile.style.height=`${change}px`;
				
			}else if(panel.scrollTop>300){
				setTile.style.height="170px";

			}
		},
	
		
	}
}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";
.set{
	position:fixed;
	top: 0;
	left: 0;
	right:0;
	bottom:0;
	z-index: 1001;
	// overflow:hidden;
	color:@color-text-l;
	background-color: @color-background-l;
	.set-title{
		background-color:rgba(0,0,0,.7);
		position: absolute;
		left: 0;
		top: 0;
		height:550px;
		width: 100%;
		z-index: 999;
		
		.set-title-wrap{
			height: 550px;
			width: 100%;
			position: absolute;
			bottom: 0;
			left: 0;
			padding:120px 32px 0;
			box-sizing: border-box;

		}
		.set-title-bg{
			position: absolute;
			left:0;
			top:0;
			width: 100%;
			height: 100%;
			filter:blur(20px);
			z-index: -1;
		}
		.set-intro{

			.list(row,display);
			.set-intro-img{
				width: 250px;
				height: 250px;
				margin-right:20px;
				img{
					width: 100%;
				}

			}
			.set-intro-detail{

				flex:1;
				.list(column);
				height:250px;
				justify-content: center;
				.set-singer{
					font-size: @font-size-large-s;
					line-height: 2;
				}
				.set-fans,.set-singer-intro
				{
					font-size:@font-size-medium;
					line-height: 1.5;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
				}
			}


		}
		.set-play-all{
			height: 168px;
			text-align: center;
			.list(column);
			justify-content: center;
			a{
				display:inline-block;
				line-height: 80px;
				width: 340px;
				text-align: center;
				background-color: @color-theme;
				color:@color-text-l;
				border-radius: 60px;

				&::before{
					content:"";
					height: 0;
					width:5px;
					display: inline-block;
					line-height: 80px;
					border:15px solid transparent;
					border-left-color:@color-text-l;
				}


			}
		}
	}
	
	

	.panel{
	
		color:@color-text-d;
		position: absolute;
		left: 0;
		top: 0;
		bottom:0;
		right: 0; 
		box-sizing: border-box;
		padding-top:550px;
	;
		overflow-y: scroll;
	}
}



</style>


