<template>
  <scroll>
    <loading v-if="loading"></loading>
<div  v-else >
   <slider :items="sliders" class="slider"></slider>
    <list :items="songListItems" :title="songListTitle"></list>
</div>
   
  </scroll>
  </template>

<script>
import Scroll from "components/base/Scroll"
import Loading from "components/base/Loading"
import {getRecommend} from 'api/recommend'
import {ERR_OK} from 'api/config.js'
import Slider from 'components/base/Slider'
import List from 'components/base/List'
import {mapMutations} from "vuex"
import jsonp from 'jsonp'

export default {
  name: 'recommend',
  components:{
    Scroll,
  	Slider,
    List,
    Loading
  },
  computed:{
...mapMutations({
  setSong:"SET_SONG"
})
  },
  data(){
  	return{ 
       sliders:[],
       songListTitle:"热门歌单",
       songListItems:[],
       loading:true
  	}
  },
  created(){
    this._getRecommend();


  },
  methods:{
  	_getRecommend(){
  		getRecommend().then(res=>{

  			if(res.code===ERR_OK){ 

  				this.sliders=res.data.slider;
          this.songListItems=res.data.songList;
          this.loading=false;
         
  			}
  		})
  	}
  }
}
</script>

<style lang="less" type="text/less" scoped>
.slider{
  margin-bottom:14px;
}
</style>



