<template>
	<div id="app" cloak>
		<div  id="app-contain-wrap">
			<m-head class="app-top"></m-head>
			
				<keep-alive>
				<router-view></router-view>
			</keep-alive>

			
		</div>
			

			<player v-if="fullScreen"></player>
	
	

			<!-- <player></player> -->
				
		
	</div>
</template>
<script>
import MHead from "components/m-head"
import Player from "components/player"
import {mapGetters} from "vuex"
export default {
	name: 'App',
	components:{
		MHead,
		Player
	},
	computed:{
		...mapGetters([
"fullScreen"
			])
	}
}

</script>
<style lang="less" type="text/less">
@import "~common/less/index";
@import "~common/less/mixin";

#app {
	// background-color: @color-background;
	
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	.list(column);
	.app-top{
		// height: 185px;
	}
	}
	// // position: relative;
	// }
	#app-contain-wrap{
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		overflow-y: scroll;
	}


</style>



