
<template>
    <Module :loaded="loaded" :imgSrc="pic_album">
    	<div slot="title">
    		<h3>{{info}}</h3>
    		<p>第{{day}}天</p>
    		<p>{{update}} 更新</p>
    	</div>
    </Module>
</template>

<script>

import Module from "components/base/Module"
import {ERR_OK} from "api/config"
import {getTop} from "api/song"
import {mapGetters} from "vuex"
	export default{ 
		name:"top",
		components:{
			Module,
		},
		computed:{
			...mapGetters([
"top"				])
		},
		methods:{
			_getTop(){
				getTop(this.top).then(res=>{
					console.log(res)

if(res.code===ERR_OK){
	let data=res.data;
	this.loaded=true;
	this.pic_album=data.topinfo.pic_album;
	this.info=data.topinfo.ilistName;
	this.update=data.date;
	this.day=data.day_of_year;

}
					

				})
			}
		},
		created(){
			if(!this.top){
				this.$router.go(-1);
				return;
			}
			
			this._getTop();
		},
		data(){
			return{
				loaded:false,
				pic_album:"",
				info:"",
				update:"",
				day:""
			}
		}

	}
    
</script>

<style lang="less" type="text/less">
    
</style>


