<template>
	<transition name="translate">

		<module :imgSrc="imgSrc" :loaded="loaded">
			
<div class="set-intro-detail" slot="title">
								<h3 class="set-singer">{{singer}}</h3>
								<p class="set-fans"><span>粉丝:</span>{{fans}}</p>
								<p class="set-singer-intro
								">{{singerDesc}}</p>
							</div>
							<div slot="list" class="set-music-list">
								<h4>歌曲 共{{total}}首</h4>
						<ul>
							<li class="list-item" v-for="(item,index) in list">
								<span>{{index+1}}</span>
								<h5>{{item.songname}}</h5>
								<p>{{item.singer}}</p>
							</li>
							<li v-if="!isAll" class="list-item load-more" @click="downloadMore">点击加载更多数</li>
							<li v-else class="list-item loaded-all">已经全部加载</li>
						</ul>
							</div>
							

		</module>
		
		
	</transition>

</template>

<script>
import Module from "components/base/Module"
import {getMusicList} from "common/js/music"
import {getSet} from "api/song"
import {ERR_OK} from "api/config"
import {mapGetters} from "vuex"
export default{ 
	name:"set",
	components:{
		Module
	},
	created(){
		if(!this.set){

			this.back();
			return;
		}
		this._getSet();

	},
	destoryed(){
	},
	methods:{
		downloadMore(){
			if(this.on){
				this.on=false;
				
			this._getSet();
			}
			
		},
		scrollEvent(){
			let setTile=this.$refs.setTitle,
			panel=this.$refs.panel;
			let change=550-panel.scrollTop;
			if(change>=170){
				console.log(change)
				
				setTile.style.height=`${change}px`;
}
		},
		_getSet(){
			getSet(this.set,this.p).then(res=>{
				if(res.code===ERR_OK){
					let data=res.data;
					this.singerDesc=data.SingerDesc;
					this.singer=data.singer_name;
					this.fans=Math.floor(data.fans/1000)/10+"万人";
					this.total=data.total;
					this.imgSrc=`https://y.gtimg.cn/music/photo_new/T001R150x150M000${data.singer_mid}.jpg?max_age=2592000`;
					data.list.forEach(item=>{
						this.list.push(getMusicList(item.musicData))
					})
					this.loaded=true;
					this.p++;
					this.on=true;

				}
			})
		},
		back(){
			this.$router.go(-1);
		},
		
	},
	computed:{
		isAll(){
			return this.list.length>=this.total;
		},
		
		...mapGetters([
			"set"
			])
	},
	data(){
		return{
			loaded:false,
			p:0,
			singerDesc:"",
			singer:"",
			fans:"",
			total:"",
			list:[],
			imgSrc:"",
			on:true
		}
	}


}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";

			.set-intro-detail{

				flex:1;
				.list(column);
				height:250px;
				justify-content: center;
				.set-singer{
					font-size: @font-size-large-s;
					line-height: 2;
				}
				.set-fans,.set-singer-intro
				{
					font-size:@font-size-medium;
					line-height: 1.5;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
				}
			}

	.set-music-list{

		color:@color-text;
		h4{
			padding:0 28px;
			font-size: @font-size-medium;
			line-height: 108px;
			color:#777;
		}
		.list-item{
			padding-left: 90px;
			position: relative;
			height: 124px; 
			.list(column);
			justify-content: center;
			line-height: 1.4;
			.no-wrap;
			span{
				position: absolute;
				color:@color-danger;
				left: 0;
				top: 0;
				line-height: 108px;
				width: 90px;
				text-align:center;
			};
			h5{
				color:rgba(0,0,0,.8);
				font-size: @font-size-large;
				.no-wrap;
			}
			p{
				font-size: @font-size-medium;
				.no-wrap;
				color:rgba(0,0,0,.6)
			}
			&.load-more,&.loaded-all{
				padding-left:0;
				text-align: center;
				font-size: @font-size-medium-x;
				color:@color-text-d;

			}
		}
	}






</style>


