<template>
  <div class="panel">
    <h3 class="panel-title">{{title}}</h3>
    <ul class="panel-list">
      <li class="panel-list-item" v-for="item in items" @click="getMusic()">
        <img v-lazy="item.picUrl" class="list-item-bg">
        <span class="list-item-accessnum"><i class="icon-erji"></i>{{item.accessnum|around}}</span>
        <i class="list-item-play icon-play"></i>
        <p class="list-item-des">{{item.songListDesc}}</p>
        <h4 class="list-item-author">{{item.songListAuthor}}</h4>
      </li>
    </ul>
  </div>
</template>
<script>
import {mapMutations} from "vuex"
export default {
  name: "list",
  methods:{
getMusic(item){
  this.setSong
}
  },
  filters: {
    around(value) {
      return Math.round(value / 1000) / 10+"万"
    }
  },
  computed:{
    ...mapMutations({
      setSong:"SET_SONG"
    })
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    items: {
      type: Array,
      default () {
        return [{
            accessnum: 0,
            album_pic_mid: "",
            id: "",
            picUrl: "",
            pic_mid: "",
            songListAuthor: "",
            songListDesc: ""
          }]
        }
      
    }
  }

}

</script>
<style lang="less" type="text/less" scoped>
@import "~common/less/index"; 
.panel{
 margin: 0 20px 14px; 

  .panel-title{ font-size: font-size-medium-x; margin-bottom:22px; } 
  .panel-list{ display: flex; flex-flow: row wrap; 
    justify-content: space-between; 
    .panel-list-item{ 
      background-color: @color-background-l;
      width:48%; 
      margin-bottom: 20px;
      position: relative; 
      .list-item-bg{ display: block; width: 100%; 
        margin-bottom:10px;} 
      .list-item-accessnum{ 
        position: absolute; left: 10px; bottom:100px; color:@color-text-l; } 
        font-size: @font-size-small;
        .icon-erji{ 
font-size: @font-size-small;
padding-right:10px;
        }
        .list-item-play{ position: absolute; right: 10px; 
          font-size: @font-size-large-x; 
          bottom:95px; color:@color-text-l; } 
          .list-item-des{ 
            text-overflow: ellipsis; 
            white-space: nowrap; 
            overflow: hidden; 
            font-size: @font-size-medium; 
            padding: 0px 14px 10px;
          } 
          .list-item-author{ .list-item-des; font-size: @font-size-small; padding-top:0;} 
          } 
          } 
        }
</style>



